import React from "react"
import { Link } from "gatsby"

import { useStaticQuery, graphql } from "gatsby"
import { rhythm, scale } from "../utils/typography"
import Image from "gatsby-image"


const Layout = ({ location, title, children }) => {
  const rootPath = `${__PATH_PREFIX__}/`
  let header

  if (location.pathname === rootPath) {
    header = (
      <h1
        style={{
          ...scale(1.5),
          marginBottom: rhythm(1.5),
          marginTop: 0,
        }}
      >
        <Link
          style={{
            boxShadow: `none`,
            color: `inherit`,
          }}
          to={`/`}
        >
          {title}
        </Link>
      </h1>
    )
  } else {
    header = (
      <h3
        style={{
          fontFamily: `Montserrat, sans-serif`,
          marginTop: 0,
        }}
      >
        <Link
          style={{
            boxShadow: `none`,
            color: `inherit`,
          }}
          to={`/`}
        >
          {title}
        </Link>
      </h3>
    )
  }
  return (
    <div
      style={{
        marginLeft: `auto`,
        marginRight: `auto`,
        maxWidth: rhythm(24),
        padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
      }}
    >
      <header>{header}</header>

      <main>{children}</main>

      <footer>
        <div class="">
          © 2020 - {new Date().getFullYear()}, Built with ❤️ by ahacad using 
          {` `}
          <a href="https://www.gatsbyjs.org">Gatsby</a>
        </div>
        <div class="">
          <Image
            alt="cc: by-nc-sd"
          />
        </div>
        <div class="">
          <a href="https://github.com/ahacad/ahacad.github.io">Make this site better by issues and prs!</a>
        </div>
      </footer>
    </div>
  )
}

export default Layout
